//     
reportWebVitals(metric => {
  //     Google Analytics    
  console.log(metric);
  
  if (window.gtag) {
    window.gtag('event', metric.name, {
      value: Math.round(metric.value), //  
      metric_id: metric.id,
      metric_value: metric.value,
      metric_delta: metric.delta,
    });
  }
});
